.twitch-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: -9999; /* Extremely low z-index */
  pointer-events: none !important; /* Force no interaction */
  overflow: hidden;
}

/* When hovering over interactive elements, hide the background completely */
.twitch-background.hovering {
  opacity: 0;
  transition: opacity 0.3s ease;
}

.twitch-player {
  position: absolute;
  top: 0;
  left: 0;
  width: 100% !important; /* Important to override Twitch's inline styles */
  height: 100% !important; /* Important to override Twitch's inline styles */
  opacity: 0.6;
  pointer-events: none !important;
}

/* Fix for Twitch iframe sizing */
.twitch-player iframe {
  border: none;
  width: 100% !important;
  height: 100% !important;
  pointer-events: none !important;
}

/* Overlay to tone down the stream visually */
.twitch-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7); /* Dark overlay */
  z-index: 1;
  pointer-events: none !important;
}

.twitch-scanlines {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    to bottom,
    rgba(0, 255, 0, 0.03) 50%,
    transparent 50%
  );
  background-size: 100% 4px;
  z-index: 2;
  animation: scanline 10s linear infinite;
  pointer-events: none !important;
}

.twitch-grid {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    linear-gradient(rgba(0, 255, 0, 0.05) 1px, transparent 1px),
    linear-gradient(90deg, rgba(0, 255, 0, 0.05) 1px, transparent 1px);
  background-size: 20px 20px;
  z-index: 3;
  animation: grid-pulse 4s ease-in-out infinite alternate;
  pointer-events: none !important;
}

.stream-info {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: -5;
  display: flex;
  align-items: center;
  gap: 10px;
}

.info-badge {
  background-color: rgba(255, 0, 0, 0.7);
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: bold;
  animation: pulse 1s infinite alternate;
}

.info-timestamp {
  color: rgba(0, 255, 0, 0.8);
  font-size: 12px;
}

.stream-loading, .stream-error {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: -4;
}

.loading-spinner {
  width: 50px;
  height: 50px;
  border: 3px solid rgba(0, 255, 0, 0.3);
  border-radius: 50%;
  border-top-color: #00ff00;
  animation: spin 1s linear infinite;
  margin-bottom: 20px;
}

.loading-text {
  color: #00ff00;
  font-size: 16px;
  letter-spacing: 2px;
}

.error-icon {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: rgba(255, 0, 0, 0.7);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px;
  margin-bottom: 20px;
}

.error-text {
  color: #ff0000;
  font-size: 18px;
  letter-spacing: 2px;
}

@keyframes scanline {
  0% { background-position: 0 0; }
  100% { background-position: 0 100%; }
}

@keyframes grid-pulse {
  0% { opacity: 0.3; }
  100% { opacity: 0.6; }
}

@keyframes pulse {
  0% { opacity: 0.7; }
  100% { opacity: 1; }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.twitch-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -10; /* Behind all content */
  pointer-events: none; /* Allow clicks to pass through */
  overflow: hidden;
}

.twitch-container iframe {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.7; /* Semi-transparent to not distract from main content */
  filter: contrast(1.2) saturate(0.8); /* Adjust for better integration */
}

.twitch-loading {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #000;
  color: #00ff00;
  font-family: 'Courier New', monospace;
  z-index: -10;
}

.dial-notch {
  transform: rotate(var(--rotation));
}