.cockpit {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #000;
  color: #00ff00;
}

/* Twitch stream container - background layer */
.twitch-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0.8; /* Increased opacity to make stream more visible */
  pointer-events: none; /* Makes clicks pass through to elements below */
}

.twitch-container iframe {
  width: 100%;
  height: 100%;
  pointer-events: none; /* Ensures the iframe cannot receive clicks */
}

/* Cockpit UI - front layer */
.cockpit-frame {
  position: relative;
  width: 100%;
  height: 100%;
  border: 15px solid rgba(51, 51, 51, 0.7); /* Make border semi-transparent */
  display: flex;
  flex-direction: column;
  background-color: rgba(0, 10, 0, 0.6); /* Reduced opacity from 0.9 to 0.6 */
  box-shadow: inset 0 0 150px rgba(0, 255, 0, 0.3);
  z-index: 2; /* Place above the twitch stream */
}

.cockpit-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 2px solid #00ff00;
  background-color: rgba(0, 0, 0, 0.5); /* More transparent */
}

.title {
  color: #00ff00;
  font-size: 2.5rem;
  text-shadow: 0 0 5px #000, 0 0 10px #000; /* Add shadow to keep text readable */
  letter-spacing: 3px;
  text-align: center;
  margin: 0;
}

.login-button {
  background-color: #222;
  color: #00ff00;
  border: 1px solid #00ff00;
  padding: 8px 15px;
  cursor: pointer;
}

.status-indicator {
  display: flex;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.7);
  padding: 5px;
  border-radius: 5px;
}

.status-light {
  width: 12px;
  height: 12px;
  background-color: #00ff00;
  border-radius: 50%;
  margin-right: 10px;
  box-shadow: 0 0 10px #00ff00;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% { opacity: 0.7; }
  50% { opacity: 1; }
  100% { opacity: 0.7; }
}

.main-display {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.nav-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 20px;
  width: 80%;
  height: 80%;
}

.nav-button {
  background-color: rgba(0, 0, 0, 0.5); /* More transparent */
  border: 2px solid #00ff00;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.3s;
}

.nav-button:hover {
  background-color: rgba(0, 255, 0, 0.1);
  transform: scale(1.02);
  box-shadow: 0 0 15px rgba(0, 255, 0, 0.5);
}

.nav-icon {
  font-size: 3rem;
  margin-bottom: 15px;
}

.cockpit-footer {
  display: flex;
  justify-content: space-between;
  padding: 10px 20px;
  background-color: rgba(0, 0, 0, 0.5); /* More transparent */
  border-top: 1px solid #00ff00;
}

/* Add scan line effect */
.cockpit-frame::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: repeating-linear-gradient(
    0deg,
    rgba(0, 0, 0, 0.15),
    rgba(0, 0, 0, 0.15) 1px,
    transparent 1px,
    transparent 2px
  );
  pointer-events: none;
  z-index: 3; /* Place scan lines above all content */
}

/* Add or update these styles in Home.css */
.home-screen {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.home-content {
  position: relative;
  z-index: 10;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 2rem;
}

.title-container {
  text-align: center;
  margin-bottom: 3rem;
}

.home-title {
  font-size: 3.5rem;
  color: #00ff00;
  text-shadow: 0 0 20px rgba(0, 255, 0, 0.8);
  margin-bottom: 0.5rem;
  letter-spacing: 6px;
  animation: title-glow 2s ease-in-out infinite alternate;
}

.home-subtitle {
  font-size: 1.4rem;
  color: #00cc00;
  letter-spacing: 2px;
  margin-top: 0;
  text-shadow: 0 0 10px rgba(0, 204, 0, 0.6);
}

/* Ensure the option links are above the background */
.home-options {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 2.5rem;
  width: 100%;
  max-width: 1200px;
  position: relative;
  z-index: 20;
}

.home-option {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 240px;
  height: 180px;
  text-decoration: none;
  color: #00ff00;
  font-size: 1.5rem;
  letter-spacing: 3px;
  border: 2px solid #00ff00;
  background-color: rgba(0, 15, 0, 0.8);
  transition: all 0.3s ease;
  position: relative;
  z-index: 30;
  cursor: pointer;
  overflow: hidden;
  box-shadow: 0 0 15px rgba(0, 255, 0, 0.3);
}

.home-option:hover {
  color: #ffff00;
  border-color: #ffff00;
  transform: scale(1.05);
  box-shadow: 0 0 25px rgba(255, 255, 0, 0.5);
  z-index: 40;
}

.option-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.home-footer {
  position: absolute;
  bottom: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  color: #00aa00;
  font-size: 1rem;
  z-index: 10;
}

/* More styles as needed */