/* Twitch stream container - background layer */
.twitch-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0.8;
  pointer-events: none;
}

.twitch-container iframe {
  width: 100%;
  height: 100%;
  pointer-events: none;
}

/* Base layout adjustments */
.cockpit {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow: hidden; /* Prevent scrolling */
  background-color: #000;
  color: #00ff00;
}

.cockpit-frame {
  position: relative;
  width: 100%;
  height: 100%;
  border: 15px solid rgba(51, 51, 51, 0.7);
  display: flex;
  flex-direction: column;
  background-color: rgba(0, 10, 0, 0.6);
  box-shadow: inset 0 0 150px rgba(0, 255, 0, 0.3);
  z-index: 2;
  overflow: hidden; /* Prevent scrolling */
}

/* Smaller header */
.cockpit-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px; /* Reduced padding */
  border-bottom: 2px solid #00ff00;
  background-color: rgba(0, 0, 0, 0.5);
  height: 60px; /* Fixed smaller height */
}

.title {
  color: #00ff00;
  font-size: 1.8rem; /* Smaller font */
  text-shadow: 0 0 10px #00ff00;
  letter-spacing: 2px;
  margin: 0;
}

.nav-button-small {
  background-color: #222;
  color: #00ff00;
  border: 1px solid #00ff00;
  padding: 8px 15px;
  cursor: pointer;
}

.status-indicator {
  display: flex;
  align-items: center;
}

.status-light {
  width: 12px;
  height: 12px;
  background-color: #00ff00;
  border-radius: 50%;
  margin-right: 10px;
  box-shadow: 0 0 10px #00ff00;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% { opacity: 0.7; }
  50% { opacity: 1; }
  100% { opacity: 0.7; }
}

/* Section content with scrolling */
.section-content {
  flex: 1;
  padding: 20px;
  overflow: hidden; /* Prevent scrolling */
}

.section-content::-webkit-scrollbar {
  display: none;
}

/* Control panel */
.control-panel {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  background-color: rgba(0, 0, 0, 0.7);
  border: 1px solid #00ff00;
}

.search-box {
  display: flex;
  align-items: center;
}

.search-input {
  width: 150px;
  height: 28px;
  font-size: 12px;
  background-color: rgba(0, 0, 0, 0.8);
}

.search-button, .upload-button {
  background-color: #222;
  border: 1px solid #00ff00;
  color: #00ff00;
  padding: 8px 15px;
  cursor: pointer;
  font-family: 'Courier New', monospace;
}

.upload-button {
  background-color: #006600;
  color: #00ff00;
  border: 1px solid #00ff00;
  padding: 8px 15px;
  cursor: pointer;
}

.categories {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.category-button {
  background-color: #111;
  border: 1px solid #00ff00;
  color: #00ff00;
  padding: 8px 15px;
  cursor: pointer;
  font-family: 'Courier New', monospace;
}

.category-button.active {
  background-color: #00ff00;
  color: #000;
}

/* Image grid */
.images-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}

.image-card {
  border: 1px solid #00ff00;
  background-color: rgba(0, 0, 0, 0.7);
  transition: all 0.3s;
  cursor: pointer;
}

.image-card:hover {
  transform: scale(1.02);
  box-shadow: 0 0 15px rgba(0, 255, 0, 0.3);
}

.image-frame {
  aspect-ratio: 16/9;
  overflow: hidden;
  position: relative;
}

.image-thumbnail {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.image-details {
  padding: 10px;
}

.image-details h3 {
  color: #00ff00;
  margin-bottom: 5px;
}

.image-metadata {
  font-size: 0.9em;
  opacity: 0.7;
  margin-top: 5px;
}

.no-results {
  text-align: center;
  padding: 50px;
  background-color: rgba(0, 0, 0, 0.7);
  border: 1px solid #00ff00;
}

/* Image Modal */
.image-modal, .upload-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
}

.modal-content {
  background-color: rgba(0, 20, 0, 0.9);
  border: 2px solid #00ff00;
  width: 80%;
  max-width: 1000px;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  border-bottom: 1px solid #00ff00;
}

.close-button {
  background: none;
  border: none;
  color: #00ff00;
  font-size: 2rem;
  cursor: pointer;
}

.modal-image {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  overflow: hidden;
}

.modal-image img {
  max-width: 100%;
  max-height: 60vh;
  object-fit: contain;
}

.modal-details {
  padding: 15px;
  border-top: 1px solid #00ff00;
}

/* Upload modal */
.modal-body {
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.modal-body input, 
.modal-body textarea, 
.modal-body select {
  background-color: #111;
  border: 1px solid #00ff00;
  color: #00ff00;
  padding: 10px;
  font-family: 'Courier New', monospace;
}

.modal-body textarea {
  min-height: 100px;
  resize: vertical;
}

.upload-preview {
  max-width: 100%;
  max-height: 200px;
  object-fit: contain;
  margin-top: 10px;
  border: 1px solid #00ff00;
}

.modal-footer {
  padding: 15px;
  border-top: 1px solid #00ff00;
  display: flex;
  justify-content: flex-end;
}

.cockpit-footer {
  display: flex;
  justify-content: space-between;
  padding: 10px 20px;
  background-color: rgba(0, 0, 0, 0.7);
  border-top: 1px solid #00ff00;
}

