/* F-16 Main Content Layout */
.f16-content {
  display: grid;
  grid-template-rows: auto 1fr;
  gap: 15px;
  height: calc(100vh - 60px); /* Adjust based on your header height */
  overflow: hidden;
}

/* F-16 Control Panel */
.f16-readouts {
  display: flex;
  gap: 20px;
}

.readout {
  display: flex;
  flex-direction: column;
}

.readout-label {
  font-size: 0.8rem;
  opacity: 0.8;
}

.readout-value {
  font-size: 1.2rem;
  color: #00ff00;
  text-shadow: 0 0 5px #00ff00;
}

/* F-16 Rotary Control Styling */

/* Layout for the controls */
.f16-display-container {
  display: grid;
  grid-template-columns: 1fr auto;
  grid-template-rows: 1fr auto;
  gap: 15px;
  grid-template-areas:
    "main-display image-dial"
    "category-dial category-dial";
  height: calc(100% - 60px);
}

.category-control {
  grid-area: category-dial;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
}

.image-control {
  grid-area: image-dial;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Make display larger to fill more space */
.f16-display {
  grid-area: main-display;
  border: 2px solid #00ff00;
  background-color: rgba(0, 0, 0, 0.7);
  padding: 15px;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

/* Rotary dial styling */
.rotary-dial {
  width: 110px;
  height: 110px;
  border-radius: 50%;
  position: relative;
  cursor: grab;
  background: radial-gradient(circle, #333333, #1a1a1a);
  border: 4px solid #444;
  box-shadow: 
    0 0 0 1px #000, 
    inset 0 0 10px rgba(0, 255, 0, 0.5),
    0 0 15px rgba(0, 255, 0, 0.3);
  margin: 10px 0;
  transition: transform 0.1s ease-out;
  transform-origin: center;
}

.rotary-dial:active {
  cursor: grabbing;
  background: radial-gradient(circle, #444444, #222222);
  transform: rotate(5deg);
}

/* Dial notches and indicators */
.dial-notch {
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  width: 8px;
  height: 20px;
  background-color: #00ff00;
  border-radius: 4px;
}

.rotary-dial::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  background-color: #333;
  border: 2px solid #555;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  box-shadow: inset 0 0 5px #000;
}

/* Additional notches around the dial */
.notch {
  position: absolute;
  width: 5px;
  height: 10px;
  background-color: #00ff00;
  transform-origin: center calc(100% + 40px); /* Position from center */
}

/* Category dial specific styling */
.category-dial {
  background: linear-gradient(135deg, #2c2c2c, #1a1a1a);
}

/* Image dial specific styling */
.image-dial {
  background: linear-gradient(135deg, #212121, #1a1a1a);
}

/* Dial labels */
.dial-label {
  color: #00ff00;
  text-align: center;
  font-size: 14px;
  margin-top: 8px;
  text-shadow: 0 0 5px #00ff00;
  font-family: 'Courier New', monospace;
}

/* Dial markings */
.dial-marking {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  transform: rotate(0deg);
}

.marking {
  position: absolute;
  background-color: rgba(0, 255, 0, 0.7);
  width: 3px;
  height: 10px;
  left: calc(50% - 1.5px);
  transform-origin: center 60px;
}

/* Dial control buttons - for manual increment */
.dial-buttons {
  display: none;
}

/* Add percentage indicator */
.dial-percentage {
  position: absolute;
  top: -25px;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.8);
  border: 1px solid #00ff00;
  color: #00ff00;
  padding: 2px 6px;
  border-radius: 3px;
  font-size: 12px;
  font-family: 'Courier New', monospace;
  z-index: 5;
}

/* Current selection indicator */
.dial-readout {
  background-color: rgba(0, 0, 0, 0.8);
  border: 1px solid #00ff00;
  color: #00ff00;
  padding: 5px 10px;
  margin: 10px 0;
  font-family: 'Courier New', monospace;
  text-align: center;
  width: 100%;
  box-shadow: inset 0 0 5px #00ff00;
}

/* Tactical display styling */
.tactical-display {
  flex: 1;
  display: flex;
  flex-direction: column;
  position: relative;
  background-color: rgba(0, 0, 0, 0.8);
  border: 1px solid #00ff00;
  padding: 10px;
  overflow: hidden;
}

.display-header {
  display: flex;
  justify-content: space-between;
  padding: 5px 10px;
  background-color: #111;
  border-bottom: 1px solid #00ff00;
  margin-bottom: 5px;
  font-family: 'Courier New', monospace;
}

.display-title {
  color: #00ff00;
  font-size: 16px;
  font-weight: bold;
}

.display-counter {
  color: #00ff00;
}

.targeting-frame {
  position: relative;
  aspect-ratio: 16/9;
  margin-bottom: 15px;
  overflow: hidden;
  border: 2px solid #00ff00;
  box-shadow: 
    inset 0 0 15px rgba(0, 255, 0, 0.5),
    0 0 10px rgba(0, 255, 0, 0.3);
}

.display-image {
  width: 100%;
  height: 100%;
  object-fit: contain; /* Fill the entire container */
  animation: projector-flicker 0.5s ease-out;
}

@keyframes projector-flicker {
  0% { opacity: 0.5; }
  10% { opacity: 0.8; }
  20% { opacity: 0.6; }
  30% { opacity: 0.9; }
  40% { opacity: 1; }
  100% { opacity: 1; }
}

.image-transition {
  animation: projector-flicker 0.5s ease-out;
}

/* Targeting reticles */
.targeting-reticle {
  position: absolute;
  width: 30px;
  height: 30px;
  pointer-events: none;
}

.targeting-reticle.top-left {
  top: 5px;
  left: 5px;
  border-top: 2px solid #00ff00;
  border-left: 2px solid #00ff00;
}

.targeting-reticle.top-right {
  top: 5px;
  right: 5px;
  border-top: 2px solid #00ff00;
  border-right: 2px solid #00ff00;
}

.targeting-reticle.bottom-left {
  bottom: 5px;
  left: 5px;
  border-bottom: 2px solid #00ff00;
  border-left: 2px solid #00ff00;
}

.targeting-reticle.bottom-right {
  bottom: 5px;
  right: 5px;
  border-bottom: 2px solid #00ff00;
  border-right: 2px solid #00ff00;
}

/* Scan line effect */
.scan-line {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background: linear-gradient(to bottom, 
    rgba(0, 255, 0, 0.5), 
    rgba(0, 255, 0, 0.2));
  box-shadow: 0 0 10px rgba(0, 255, 0, 0.5);
  animation: scan 2s linear infinite;
  pointer-events: none;
  z-index: 3;
}

@keyframes scan {
  0% { top: 0; }
  100% { top: 100%; }
}

/* Add film grain effect for more realism */
.tactical-display::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 200 200' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.65' numOctaves='3' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noiseFilter)'/%3E%3C/svg%3E");
  opacity: 0.03;
  pointer-events: none;
  z-index: 2;
}

