/* F-16 Cockpit Interface Styling */

/* Base layout */
.f16-cockpit {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #111111;
  padding: 0;
  overflow: hidden;
  position: relative;
  font-family: 'Courier New', monospace;
  color: #00ff00;
}

/* Cockpit frame styling */
.cockpit-frame {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 10;
}

.frame-top, .frame-bottom, .frame-left, .frame-right {
  position: absolute;
  background-color: #333;
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.8);
}

.frame-top, .frame-bottom {
  left: 0;
  width: 100%;
  height: 20px;
}

.frame-top {
  top: 0;
  border-bottom: 2px solid #444;
}

.frame-bottom {
  bottom: 0;
  border-top: 2px solid #444;
}

.frame-left, .frame-right {
  top: 0;
  height: 100%;
  width: 20px;
}

.frame-left {
  left: 0;
  border-right: 2px solid #444;
}

.frame-right {
  right: 0;
  border-left: 2px solid #444;
}

.rivet {
  position: absolute;
  width: 10px;
  height: 10px;
  background: radial-gradient(circle, #666, #333);
  border-radius: 50%;
  box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.8);
}

.rivet-1 { top: 5px; left: 20px; }
.rivet-2 { top: 5px; right: 20px; }
.rivet-3 { bottom: 5px; left: 20px; }
.rivet-4 { bottom: 5px; right: 20px; }
.rivet-5 { top: 50%; left: 5px; }
.rivet-6 { top: 50%; right: 5px; }
.rivet-7 { top: 30%; left: 5px; }
