/* Keep only pilot vitals with better layout */
.pilot-vitals {
  width: 100%;
  display: flex;
  justify-content: space-around;
  margin-top: 10px;
}

/* Mini-map section */
.mini-map-container {
  flex: 2; /* Larger proportion */
  display: flex;
  flex-direction: column;
  border: 1px solid #0f0;
  background-color: rgba(0, 10, 0, 0.7);
  box-shadow: inset 0 0 15px rgba(0, 40, 0, 0.7);
}

.mini-map {
  flex: 1;
  position: relative;
  display: flex;
  overflow: hidden;
}

.map-grid {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image:
    linear-gradient(#00ff00 1px, transparent 1px),
    linear-gradient(90deg, #00ff00 1px, transparent 1px);
  background-size: 15px 15px;
  opacity: 0.3;
}

.map-position {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 6px;
  height: 6px;
  background-color: #00ff00;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 8px #00ff00;
  animation: pulse 1.5s infinite;
}

@keyframes pulse {
  0% { box-shadow: 0 0 8px #00ff00; }
  50% { box-shadow: 0 0 15px #00ff00; }
  100% { box-shadow: 0 0 8px #00ff00; }
}

.coordinates {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 12px;
  color: #00ff00;
}

/* Navigation controls */
.nav-controls {
  position: relative; /* Not fixed/absolute */
  bottom: auto;
  right: auto;
  width: 100%;
  background: rgba(0, 20, 0, 0.8);
  border: 1px solid #0f0;
  padding: 8px;
  display: flex;
  justify-content: space-between;
  gap: 15px;
  margin-bottom: 10px; /* Space between nav and status bar */
  z-index: 10;
}

.nav-btn, .upload-btn {
  background-color: #003300;
  color: #00ff00;
  border: 1px solid #00ff00;
  padding: 5px 10px;
  font-size: 12px;
  cursor: pointer;
  font-family: 'Courier New', monospace;
  font-weight: bold;
  transition: all 0.2s;
  box-shadow: 0 0 10px rgba(0, 255, 0, 0.3);
  text-align: center;
}

.nav-btn:hover, .upload-btn:hover {
  background-color: #004400;
  box-shadow: 0 0 15px rgba(0, 255, 0, 0.5);
}

.nav-btn:active, .upload-btn:active {
  background-color: #002200;
  transform: scale(0.95);
}

.upload-btn {
  background-color: #006600;
  display: block;
}

.hidden-input {
  display: none;
}

/* Scrollbar styling */
::-webkit-scrollbar {
  width: 12px;
  height: 12px;
}

::-webkit-scrollbar-track {
  background: #001100;
}
 
::-webkit-scrollbar-thumb {
  background: #00aa00;
  border: 2px solid #001100;
}

::-webkit-scrollbar-thumb:hover {
  background: #00cc00;
}

/* Custom scrollbar for Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #00aa00 #001100;
}

/* Responsive adjustments for smaller screens */
@media (max-width: 1024px) {
  .status-bar {
    flex-direction: column;
    max-height: none;
  }
  
  .pilot-status, .mini-map-container {
    flex: 0 0 auto;
    width: 100%;
    border-right: none;
    border-bottom: 2px solid #00ff00;
    padding-bottom: 10px;
    margin-bottom: 10px;
  }
  
  .nav-controls {
    padding: 10px 0;
  }
}

@media (max-width: 768px) {
  .terminal-header {
    flex-direction: column;
    gap: 10px;
  }
  
  .section-nav {
    width: 100%;
    justify-content: space-between;
  }
}

/* F-16 terminal styling */
.f16-terminal {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  background-color: #000;
  color: #0f0;
  overflow: hidden;
  font-family: 'Share Tech Mono', monospace;
}

/* Variables for consistent styling */
:root {
  --terminal-bg: #000;
  --terminal-text: #0f0;
  --terminal-accent: #0ff;
  --terminal-highlight: #ff0;
  --terminal-warning: #f00;
  --terminal-success: #0f0;
  --terminal-header: #111;
  --terminal-border: #0f0;
  
  /* Dynamic values that will be updated by the component */
  --health-percent: 92%;
  --ammo-percent: 84%;
  --armor-percent: 76%;
}

/* Header styling */
.terminal-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: rgba(0, 0, 0, 0.9);
  padding: 0.5rem 1rem;
  border-bottom: 1px solid var(--terminal-border);
  height: 50px;
  width: 100%;
  box-shadow: 0 0 15px rgba(0, 255, 0, 0.2);
}

.home-link {
  color: var(--terminal-accent);
  text-decoration: none;
  font-size: 1.2rem;
  margin-right: 2rem;
  position: relative;
  padding-left: 1.5rem;
  text-shadow: 0 0 5px var(--terminal-accent);
  letter-spacing: 1px;
}

.home-link::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  font-size: 0.8em;
  animation: blink 1s infinite;
}

.section-nav {
  display: flex;
  gap: 1.5rem;
  flex-grow: 1;
  justify-content: flex-end;
}

.section-link {
  color: var(--terminal-text);
  text-decoration: none;
  padding: 0.25rem 0.75rem;
  border: 1px solid rgba(0, 255, 0, 0.4);
  background: rgba(0, 30, 0, 0.5);
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
  position: relative;
  overflow: hidden;
}

.section-link::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, var(--terminal-text), transparent);
  transform: translateX(-100%);
  transition: transform 0.5s ease;
}

.section-link:hover::after {
  transform: translateX(100%);
}

.section-link.active {
  color: var(--terminal-highlight);
  border-color: var(--terminal-highlight);
  box-shadow: 0 0 10px rgba(255, 255, 0, 0.3);
  background: rgba(40, 40, 0, 0.3);
}

/* Category bar styling */
.category-bar {
  display: flex;
  overflow-x: auto;
  background-color: rgba(0, 0, 0, 0.9);
  padding: 0.5rem;
  border-bottom: 1px solid var(--terminal-border);
  height: 45px;
  width: 100%;
  scrollbar-width: thin;
  scrollbar-color: var(--terminal-accent) var(--terminal-bg);
}

.category-bar::-webkit-scrollbar {
  height: 5px;
}

.category-bar::-webkit-scrollbar-track {
  background: var(--terminal-bg);
}

.category-bar::-webkit-scrollbar-thumb {
  background-color: var(--terminal-accent);
  border-radius: 10px;
}

.category-btn {
  background: none;
  border: 1px solid var(--terminal-text);
  color: var(--terminal-text);
  padding: 0.25rem 0.75rem;
  margin-right: 0.5rem;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: 'Share Tech Mono', monospace;
  text-transform: uppercase;
  letter-spacing: 1px;
  position: relative;
  overflow: hidden;
  white-space: nowrap;
  min-width: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.category-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(0, 255, 0, 0.1), transparent);
  transition: left 0.5s;
}

.category-btn:hover::before {
  left: 100%;
}

.category-btn:hover {
  background-color: rgba(0, 40, 0, 0.5);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 255, 0, 0.1);
}

.category-btn.active {
  background-color: rgba(0, 60, 0, 0.3);
  border-color: var(--terminal-highlight);
  color: var(--terminal-highlight);
  box-shadow: 0 0 15px var(--terminal-highlight);
}

/* Section navigation styling - NEW */
.section-navigation {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 1rem;
  background-color: rgba(0, 20, 0, 0.8);
  border-bottom: 1px solid var(--terminal-border);
  height: 40px;
}

.section-nav-btn {
  background: rgba(0, 40, 0, 0.5);
  border: 1px solid var(--terminal-accent);
  color: var(--terminal-accent);
  padding: 0.25rem 1rem;
  cursor: pointer;
  font-family: 'Share Tech Mono', monospace;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.section-nav-btn::after {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 20px;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(0, 255, 255, 0.3), transparent);
  animation: shimmer 1.5s infinite;
}

.section-nav-btn:hover {
  background-color: rgba(0, 60, 60, 0.5);
  box-shadow: 0 0 10px var(--terminal-accent);
}

.section-counter {
  font-size: 0.9rem;
  color: var(--terminal-accent);
  background-color: rgba(0, 0, 0, 0.5);
  padding: 0.25rem 1rem;
  border: 1px solid rgba(0, 255, 255, 0.3);
  letter-spacing: 1px;
}

/* Main display area styling */
.main-display {
  flex: 5; /* Takes more space vertically */
  display: flex;
  flex-direction: column;
  padding: 8px;
  overflow: hidden;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.8);
}

.display-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
  background-color: rgba(0, 20, 0, 0.8);
  border-bottom: 1px solid rgba(0, 255, 0, 0.3);
  height: 40px;
}

.display-title {
  font-size: 1.2rem;
  color: var(--terminal-accent);
  text-shadow: 0 0 5px var(--terminal-accent);
  letter-spacing: 2px;
  position: relative;
  padding-left: 20px;
}

.display-title::before {
  content: '';
  position: absolute;
  left: 0;
  color: var(--terminal-highlight);
  animation: blink 1s infinite;
}

.display-counter {
  font-size: 0.9rem;
  background-color: rgba(0, 40, 40, 0.3);
  padding: 0.25rem 0.75rem;
  border: 1px solid var(--terminal-accent);
  color: var(--terminal-highlight);
  letter-spacing: 1px;
  position: relative;
  overflow: hidden;
}

.display-counter::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at center, rgba(0, 255, 255, 0.2), transparent);
  animation: pulse 2s infinite;
  pointer-events: none;
}

/* Tactical display styling - IMPROVED SIZE */
.tactical-display {
  flex: 1;
  display: flex;
  gap: 1rem;
  padding: 1rem;
  height: calc(100% - 40px);
  width: 100%;
}

.targeting-frame {
  width: 100%; /* Full width */
  height: 75%; /* Takes most of the tactical display height */
  position: relative;
  border: 2px solid #0f0;
  overflow: hidden;
}

.display-image {
  width: 100%;
  height: 100%;
  object-fit: contain; /* Changed to contain to show full image */
  filter: contrast(1.1) brightness(0.95);
  transition: all 0.3s ease;
}

/* Targeting reticle effects */
.targeting-reticle {
  position: absolute;
  width: 50px;
  height: 50px;
  pointer-events: none;
}

.top-left {
  top: 10px;
  left: 10px;
  border-top: 2px solid var(--terminal-highlight);
  border-left: 2px solid var(--terminal-highlight);
  animation: pulse-corner 3s infinite;
}

.top-right {
  top: 10px;
  right: 10px;
  border-top: 2px solid var(--terminal-highlight);
  border-right: 2px solid var(--terminal-highlight);
  animation: pulse-corner 3s infinite 0.75s;
}

.bottom-left {
  bottom: 10px;
  left: 10px;
  border-bottom: 2px solid var(--terminal-highlight);
  border-left: 2px solid var(--terminal-highlight);
  animation: pulse-corner 3s infinite 1.5s;
}

.bottom-right {
  bottom: 10px;
  right: 10px;
  border-bottom: 2px solid var(--terminal-highlight);
  border-right: 2px solid var(--terminal-highlight);
  animation: pulse-corner 3s infinite 2.25s;
}

@keyframes pulse-corner {
  0%, 100% {
    opacity: 0.5;
    box-shadow: none;
  }
  50% {
    opacity: 1;
    box-shadow: 0 0 5px var(--terminal-highlight);
  }
}

.scan-line {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: rgba(0, 255, 255, 0.7);
  box-shadow: 0 0 10px var(--terminal-accent);
  pointer-events: none;
  animation: scan 4s linear infinite;
  z-index: 5;
}

.grid-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    linear-gradient(rgba(0, 255, 0, 0.03) 1px, transparent 1px),
    linear-gradient(90deg, rgba(0, 255, 0, 0.03) 1px, transparent 1px);
  background-size: 20px 20px;
  pointer-events: none;
}

/* Image info panel */
.image-intel {
  width: 100%;
  height: 25%; /* Smaller portion below the image */
  padding: 10px;
  background-color: rgba(0, 20, 0, 0.8);
  border: 1px solid #0f0;
  display: flex;
  flex-direction: column;
  box-shadow: inset 0 0 10px rgba(0, 255, 0, 0.3);
}

.intel-title {
  color: var(--terminal-accent);
  margin-bottom: 1rem;
  font-size: 1.2rem;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-shadow: 0 0 5px var(--terminal-accent);
  position: relative;
  padding-bottom: 10px;
}

.intel-title::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: linear-gradient(90deg, var(--terminal-accent), transparent);
}

.intel-desc {
  flex: 1;
  margin-bottom: 1rem;
  overflow-y: auto;
  font-size: 0.9rem;
  line-height: 1.4;
  padding: 5px;
  color: #cfc;
}

.intel-meta {
  font-size: 0.9rem;
  color: var(--terminal-highlight);
  border-top: 1px solid rgba(255, 255, 0, 0.3);
  padding-top: 0.5rem;
  letter-spacing: 1px;
}

.no-intel {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  color: var(--terminal-warning);
  background-color: rgba(40, 0, 0, 0.3);
  border: 1px solid var(--terminal-warning);
  position: relative;
  overflow: hidden;
}

.no-intel::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 200%;
  height: 100%;
  background: repeating-linear-gradient(
    45deg,
    rgba(255, 0, 0, 0.1),
    rgba(255, 0, 0, 0.1) 10px,
    transparent 10px,
    transparent 20px
  );
  animation: slide-warn 10s linear infinite;
}

.no-intel p {
  margin: 0.8rem 0;
  text-align: center;
  text-shadow: 0 0 5px rgba(255, 0, 0, 0.7);
  letter-spacing: 1px;
  font-size: 1.2rem;
  z-index: 1;
  backdrop-filter: blur(2px);
  padding: 0.5rem 1rem;
  background-color: rgba(0, 0, 0, 0.6);
  width: 80%;
}

/* Status bar styling - REDUCED HEIGHT */
.status-bar {
  height: 140px; /* Fixed height */
  background-color: rgba(0, 0, 0, 0.9);
  border-top: 2px solid #0f0;
  display: flex;
  padding: 8px;
  gap: 10px;
}

/* Pilot status styling - ENHANCED */
.pilot-status {
  flex: 1;
  display: flex;
  gap: 1rem;
  width: 30%;
  padding: 0.5rem;
  border: 1px solid rgba(0, 255, 0, 0.4);
  background-color: rgba(0, 15, 0, 0.7);
  box-shadow: inset 0 0 15px rgba(0, 40, 0, 0.7);
  position: relative;
  overflow: hidden;
}

.pilot-status::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at center, rgba(0, 255, 0, 0.1), transparent 70%);
  z-index: 0;
}

.pilot-face {
  width: 80px;
  height: 100%;
  background-color: #111;
  background-image: url('data:image/svg+xml;base64,'); /* Base64 SVG pilot face */
  background-size: cover;
  border: 2px solid var(--terminal-accent);
  z-index: 1;
  filter: brightness(1.1) contrast(1.2);
  box-shadow: 0 0 10px rgba(0, 255, 255, 0.5);
  animation: pulse-pilot 2s infinite;
}

@keyframes pulse-pilot {
  0%, 100% {
    box-shadow: 0 0 5px rgba(0, 255, 255, 0.5);
  }
  50% {
    box-shadow: 0 0 15px rgba(0, 255, 255, 0.8);
  }
}

.face-0 { filter: brightness(1) contrast(1.2); } /* Normal */
.face-1 { filter: brightness(1.3) contrast(1.1) hue-rotate(10deg); } /* Alert */
.face-2 { filter: brightness(0.7) contrast(1.5) hue-rotate(-20deg); } /* Hurt */

/* Remove stat bars */
.stat-bars {
  display: none;
}

/* Keep only pilot vitals with better layout */
.pilot-vitals {
  width: 100%;
  display: flex;
  justify-content: space-around;
  margin-top: 10px;
}

/* Mini map and coordinates styling - ENHANCED */
.mini-map-container {
  flex: 2; /* Takes twice as much space as pilot status */
  display: flex;
  flex-direction: column;
  border: 1px solid #0f0;
  background-color: rgba(0, 10, 0, 0.7);
  box-shadow: inset 0 0 15px rgba(0, 40, 0, 0.7);
}

.mini-map {
  flex: 1;
  position: relative;
  display: flex;
  overflow: hidden;
}

.map-grid {
  position: absolute;
  width: 100%;
  height: 100%;
  background:
    linear-gradient(rgba(0, 255, 0, 0.05) 1px, transparent 1px),
    linear-gradient(90deg, rgba(0, 255, 0, 0.05) 1px, transparent 1px);
  background-size: 20px 20px;
  z-index: 1;
}

.map-position {
  position: absolute;
  width: 10px;
  height: 10px;
  background-color: var(--terminal-highlight);
  border-radius: 50%;
  z-index: 10;
  box-shadow: 0 0 10px var(--terminal-highlight);
  animation: pulse-position 1.5s infinite alternate;
  z-index: 10;
}

@keyframes pulse-position {
  0% {
    transform: scale(0.8);
    opacity: 0.7;
  }
  100% {
    transform: scale(1.2);
    opacity: 1;
  }
}

/* 3D terrain visualization - ENHANCED */
.terrain-map {
  width: 100%; /* Take full width now */
  height: 100%;
  position: relative;
  background: linear-gradient(
    to bottom,
    #001800,
    #002800
  );
  perspective: 500px;
  overflow: hidden;
  box-shadow: inset 0 0 20px rgba(0, 0, 0, 0.7);
}

.terrain-grid {
  position: absolute;
  width: 200%;
  height: 400%;
  top: -50%;
  left: -50%;
  background: repeating-linear-gradient(
    to right,
    rgba(0, 255, 0, 0.1),
    rgba(0, 255, 0, 0.1) 1px,
    transparent 1px,
    transparent 20px
  ),
  repeating-linear-gradient(
    to bottom,
    rgba(0, 255, 0, 0.1),
    rgba(0, 255, 0, 0.1) 1px,
    transparent 1px,
    transparent 20px
  );
  transform: rotateX(60deg);
  animation: terrainMove 10s linear infinite;
  z-index: 1;
}

@keyframes terrainMove {
  0% {
    transform: rotateX(60deg) translateY(0);
  }
  100% {
    transform: rotateX(60deg) translateY(20%);
  }
}

.terrain-features {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: 
    radial-gradient(circle at 30% 70%, rgba(0, 255, 0, 0.2) 0%, rgba(0, 255, 0, 0) 20%),
    radial-gradient(circle at 70% 30%, rgba(0, 255, 0, 0.2) 0%, rgba(0, 255, 0, 0) 20%),
    radial-gradient(circle at 50% 50%, rgba(0, 255, 0, 0.2) 0%, rgba(0, 255, 0, 0) 30%);
  z-index: 2;
}

.terrain-mountains {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 40%;
  background-image: 
    linear-gradient(transparent 0%, transparent 50%, rgba(0, 100, 0, 0.3) 100%),
    linear-gradient(135deg, transparent 0%, transparent 50%, rgba(0, 100, 0, 0.3) 51%, transparent 51%, transparent 100%),
    linear-gradient(45deg, transparent 0%, transparent 50%, rgba(0, 100, 0, 0.3) 51%, transparent 51%, transparent 100%);
  background-size: 100% 100%, 40px 40px, 40px 40px;
  z-index: 3;
  transform-origin: bottom;
  animation: mountains-pulse 8s ease-in-out infinite alternate;
}

@keyframes mountains-pulse {
  0% {
    opacity: 0.7;
    transform: scaleY(1);
  }
  100% {
    opacity: 1;
    transform: scaleY(1.1);
  }
}

.flight-path {
  position: absolute;
  width: 150%;
  height: 1px;
  top: 50%;
  left: -25%;
  background-color: rgba(255, 255, 0, 0.5);
  transform: translateY(-50%) rotateX(60deg);
  z-index: 5;
  box-shadow: 0 0 5px var(--terminal-highlight);
}

.flight-path:nth-child(1) {
  top: 40%;
  animation: path-move 5s linear infinite;
}

.flight-path:nth-child(2) {
  top: 50%;
  animation: path-move 5s linear infinite 1.67s;
}

.flight-path:nth-child(3) {
  top: 60%;
  animation: path-move 5s linear infinite 3.33s;
}

@keyframes path-move {
  0% {
    transform: translateY(-50%) rotateX(60deg) translateX(-100%);
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    transform: translateY(-50%) rotateX(60deg) translateX(100%);
    opacity: 0;
  }
}

.coordinates {
  height: 30px;
  display: flex;
  justify-content: space-around;
