  font-size: 8px;
  color: #0f0;
  text-shadow: 0 0 2px #0f0;
  display: flex;
  flex-direction: column;
}

/* Terrain section */
.terrain-section {
  flex: 1;
  height: 100%;
  padding: 5px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.terrain-view {
  flex: 1;
  position: relative;
  background: linear-gradient(to bottom, #001200, #002800);
  border: 1px solid rgba(0, 255, 0, 0.4);
  min-height: 0;
}

.terrain-grid {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image:
    linear-gradient(rgba(0, 255, 0, 0.1) 1px, transparent 1px),
    linear-gradient(90deg, rgba(0, 255, 0, 0.1) 1px, transparent 1px);
  background-size: 20px 5px;
  perspective: 300px;
  transform: rotateX(60deg);
  transform-origin: bottom;
}

.terrain-features {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 40%;
  background-image: radial-gradient(
    circle at 50% 120%,
    rgba(0, 255, 0, 0.2) 0%,
    transparent 70%
  );
}

.terrain-mountains {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 20%;
  background-image:
    linear-gradient(170deg, transparent 0%, rgba(0, 100, 0, 0.3) 100%),
    linear-gradient(190deg, transparent 0%, rgba(0, 100, 0, 0.3) 100%);
}

.flight-path {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image:
    linear-gradient(90deg, rgba(0, 255, 0, 0.2) 0%, rgba(0, 255, 0, 0) 100%);
  clip-path: polygon(50% 100%, 60% 0%, 40% 0%);
  animation: path-pulse 2s infinite alternate;
}

.flight-path.secondary {
  clip-path: polygon(20% 100%, 25% 0%, 15% 0%);
  opacity: 0.6;
}

.terrain-info {
  position: absolute;
  bottom: 3px;
  right: 5px;
  font-size: 8px;
  color: #0f0;
}

/* Controls panel */
.category-control, .image-control {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: rgba(0, 10, 0, 0.7);
  border: 1px solid #0f0;
  box-shadow: inset 0 0 15px rgba(0, 40, 0, 0.7);
  padding: 10px;
}

.dial-label {
  font-size: 12px;
  font-weight: bold;
  text-shadow: 0 0 5px rgba(0, 255, 0, 0.7);
  margin-bottom: 5px;
}

.rotary-dial {
  width: 80px;
  height: 80px;
  background-color: #111;
  border: 2px solid #0f0;
  border-radius: 50%;
  position: relative;
  margin: 5px 0;
}

.dial-marking {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 2px;
  height: 10px;
  background-color: #0f0;
}

.dial-notch {
  position: absolute;
  top: 0;
  left: 50%;
  transform-origin: bottom center;
  width: 4px;
  height: 30px;
  background-color: #0f0;
  box-shadow: 0 0 5px #0f0;
  transition: transform 0.3s ease;
}

.dial-percentage {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 14px;
  color: #0f0;
  text-shadow: 0 0 5px #0f0;
}

.dial-readout {
  margin: 5px 0;
  font-size: 12px;
  color: #0f0;
  text-shadow: 0 0 3px #0f0;
}

.dial-buttons {
  display: flex;
  gap: 10px;
}

.dial-button {
  background-color: rgba(0, 30, 0, 0.8);
  border: 1px solid #0f0;
  color: #0f0;
  padding: 5px 10px;
  font-size: 10px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.dial-button:hover {
  background-color: rgba(0, 50, 0, 0.9);
  box-shadow: 0 0 5px rgba(0, 255, 0, 0.7);
}

/* Main display area */
.main-display-area {
  flex: 1;
  display: flex;
  gap: 15px;
  overflow: hidden;
}

.tactical-display {
  flex: 3;
  display: flex;
  flex-direction: column;
  background-color: rgba(0, 10, 0, 0.7);
  border: 1px solid #0f0;
  box-shadow: inset 0 0 15px rgba(0, 40, 0, 0.7);
  overflow: hidden;
}

.display-header {
  display: flex;
  justify-content: space-between;
  background-color: rgba(0, 20, 0, 0.8);
  padding: 5px 10px;
  border-bottom: 1px solid #0f0;
}

.display-title {
  font-size: 14px;
  font-weight: bold;
}

.display-counter {
  font-size: 14px;
}

.targeting-frame {
  flex: 1;
  position: relative;
  overflow: hidden;
  background-color: #000;
  display: flex;
  align-items: center;
  justify-content: center;
}

.display-image {
  max-width: 100%;
  max-height: 100%;
  transition: opacity 0.3s ease;
}

.image-transition {
  opacity: 0.5;
}

.no-image-placeholder {
  color: #f00;
  font-size: 1.5rem;
  text-shadow: 0 0 5px #f00;
}

.targeting-reticle {
  position: absolute;
  width: 20px;
  height: 20px;
  border: 2px solid rgba(0, 255, 0, 0.7);
}

.targeting-reticle.top-left {
  top: 10px;
  left: 10px;
  border-right: none;
  border-bottom: none;
}

.targeting-reticle.top-right {
  top: 10px;
  right: 10px;
  border-left: none;
  border-bottom: none;
}

.targeting-reticle.bottom-left {
  bottom: 10px;
  left: 10px;
  border-right: none;
  border-top: none;
}

.targeting-reticle.bottom-right {
  bottom: 10px;
  right: 10px;
  border-left: none;
  border-top: none;
}

.scan-line {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: rgba(0, 255, 0, 0.3);
  box-shadow: 0 0 10px rgba(0, 255, 0, 0.5);
  z-index: 2;
  animation: scan-down 4s linear infinite;
}

.grid-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image:
    linear-gradient(rgba(0, 255, 0, 0.05) 1px, transparent 1px),
    linear-gradient(90deg, rgba(0, 255, 0, 0.05) 1px, transparent 1px);
  background-size: 20px 20px;
  pointer-events: none;
}

.image-intel {
  padding: 10px;
  background-color: rgba(0, 15, 0, 0.8);
  border-top: 1px solid #0f0;
}

.intel-title {
  margin: 0 0 5px;
  font-size: 16px;
  color: #0ff;
}

.intel-desc {
  margin: 0 0 5px;
  font-size: 14px;
  line-height: 1.4;
}

.intel-meta {
  font-size: 12px;
  color: #aaa;
  margin-top: 5px;
}

.no-intel {
  color: #f00;
  font-style: italic;
}

.action-buttons {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background-color: rgba(0, 20, 0, 0.8);
  border-top: 1px solid #0f0;
}

.tactical-button {
  background-color: rgba(0, 40, 0, 0.8);
  border: 1px solid #0f0;
  color: #0f0;
  padding: 8px 15px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.tactical-button:hover {
  background-color: rgba(0, 60, 0, 0.9);
  box-shadow: 0 0 10px rgba(0, 255, 0, 0.7);
}

.delete-button {
  background-color: rgba(40, 0, 0, 0.8);
  border: 1px solid #f00;
  color: #f00;
}

.delete-button:hover {
  background-color: rgba(60, 0, 0, 0.9);
  box-shadow: 0 0 10px rgba(255, 0, 0, 0.5);
}

.delete-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Comment container */
.comment-container {
  flex: 2;
  display: flex;
  flex-direction: column;
  max-width: 400px;
  overflow: hidden;
}

/* Animations */
@keyframes blink {
  0% { opacity: 1; }
  100% { opacity: 0.3; }
}

@keyframes scan-down {
  0% { top: 0; }
  100% { top: 100%; }
}

@keyframes face-scan {
  0% { top: 0; }
  100% { top: 100%; }
}

@keyframes position-move {
  0% { top: 30%; left: 30%; }
  25% { top: 40%; left: 70%; }
  50% { top: 70%; left: 50%; }
  75% { top: 60%; left: 20%; }
  100% { top: 30%; left: 30%; }
}

@keyframes path-pulse {
  0% { opacity: 0.2; }
  100% { opacity: 0.6; }
}

/* Responsive adjustments */
@media (max-width: 1200px) {
  .main-display-area {
    flex-direction: column;
  }
  
  .comment-container {
    max-width: 100%;
    height: 300px;
  }
}

@media (max-width: 768px) {
  .top-section-layout {
    flex-direction: column;
    height: auto;
  }
  
  .status-panel, .controls-panel {
    height: 180px;
  }
}

