/* Inherit base styles from Images.css */
@import './Images.css';

/* Base styles */
body {
  margin: 0;
  padding: 0;
  font-family: 'Share Tech Mono', monospace, sans-serif;
}

/* Video display specific styling */
.video-display {
  flex: 1;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
}

.video-terminal {
  width: 100vw;
  height: 100vh;
  background-color: #000;
  color: #00ff00;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/* Header */
.terminal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
  padding: 0 15px;
  background-color: #001100;
  border-bottom: 1px solid #00ff00;
}

.home-link {
  color: #00ff00;
  text-decoration: none;
  font-weight: bold;
  letter-spacing: 1px;
}

.home-link:hover {
  color: #ffff00;
  text-shadow: 0 0 8px rgba(255, 255, 0, 0.7);
}

.section-nav {
  display: flex;
  gap: 15px;
}

.section-link {
  color: #00aa00;
  text-decoration: none;
  padding: 5px 10px;
}

.section-link.active {
  color: #ffff00;
  border-bottom: 2px solid #ffff00;
}

.section-link:hover {
  color: #ffff00;
}

/* Video Player Container - takes all available space */
.video-player-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  position: relative;
}

/* Video Frame */
.video-frame {
  flex: 1;
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  box-shadow: inset 0 0 30px rgba(0, 255, 0, 0.3);
  background-color: #000;
}

.video-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
  background-color: #001100;
  filter: contrast(1.1) saturate(0.9);
  animation: video-flicker 5s infinite;
}

@keyframes video-flicker {
  0%, 100% { filter: contrast(1.1) saturate(0.9); }
  25% { filter: contrast(1.2) saturate(0.85) brightness(1.05); }
  50% { filter: contrast(1.1) saturate(0.92) brightness(0.98); }
  75% { filter: contrast(1.15) saturate(0.88) brightness(1.02); }
}

/* Targeting reticles */
.targeting-reticle {
  position: absolute;
  width: 40px;
  height: 40px;
}

.top-left {
  top: 10px;
  left: 10px;
  border-top: 2px solid #00ff00;
  border-left: 2px solid #00ff00;
}

.top-right {
  top: 10px;
  right: 10px;
  border-top: 2px solid #00ff00;
  border-right: 2px solid #00ff00;
}

.bottom-left {
  bottom: 10px;
  left: 10px;
  border-bottom: 2px solid #00ff00;
  border-left: 2px solid #00ff00;
}

.bottom-right {
  bottom: 10px;
  right: 10px;
  border-bottom: 2px solid #00ff00;
  border-right: 2px solid #00ff00;
}

/* Scan line */
.scan-line {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, transparent 50%, rgba(0, 255, 0, 0.05) 50%);
  background-size: 100% 4px;
  pointer-events: none;
  z-index: 2;
}

/* Grid overlay */
.grid-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    linear-gradient(rgba(0, 255, 0, 0.05) 1px, transparent 1px),
    linear-gradient(90deg, rgba(0, 255, 0, 0.05) 1px, transparent 1px);
  background-size: 20px 20px;
  pointer-events: none;
  z-index: 1;
}

/* Video info */
.video-info {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  color: #00ff00;
  font-family: 'Share Tech Mono', monospace;
  font-size: 12px;
  text-shadow: 0 0 5px rgba(0, 255, 0, 0.7);
  z-index: 3;
}

.info-top-left {
  position: absolute;
  top: 15px;
  left: 60px;
  font-size: 14px;
}

.info-top-right {
  position: absolute;
  top: 15px;
  right: 60px;
  text-align: right;
  font-size: 14px;
}

.info-bottom {
  position: absolute;
  bottom: 20px;
  left: 0;
  width: 100%;
  padding: 10px 20px;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: space-between;
  border-top: 1px solid rgba(0, 255, 0, 0.3);
}

.timestamp, .feed-id, .security, .recording, .location, .subject {
  margin: 5px 0;
}

.blink {
  animation: blink 1s infinite alternate;
}

@keyframes blink {
  from { opacity: 1; }
  to { opacity: 0.3; }
}

@keyframes blink-animation {
  0%, 49% { opacity: 1; }
  50%, 100% { opacity: 0.3; }
}

/* Video controls */
.video-controls {
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  background-color: rgba(0, 0, 0, 0.9);
  border-top: 1px solid #00ff00;
}

.control-btn {
  background-color: rgba(0, 30, 0, 0.8);
  color: #00ff00;
  border: 1px solid #00ff00;
  padding: 5px 15px;
  font-family: 'Share Tech Mono', monospace;
  cursor: pointer;
  transition: all 0.2s;
  text-shadow: 0 0 5px rgba(0, 255, 0, 0.5);
}

.control-btn:hover {
  background-color: rgba(0, 60, 0, 0.8);
  box-shadow: 0 0 10px rgba(0, 255, 0, 0.4);
}

.speed-controls {
  display: flex;
  align-items: center;
}

.speed-label {
  margin-right: 10px;
  color: #00ff00;
}

.speed-btn {
  background-color: rgba(0, 20, 0, 0.8);
  color: #00ff00;
  border: 1px solid #00ff00;
  padding: 3px 10px;
  margin: 0 3px;
  cursor: pointer;
  font-family: 'Share Tech Mono', monospace;
  font-size: 12px;
}

.speed-btn.active {
  background-color: rgba(0, 80, 0, 0.8);
  box-shadow: inset 0 0 8px rgba(0, 255, 0, 0.5);
}

.feed-info {
  display: flex;
  gap: 15px;
  font-size: 12px;
  color: #00ff00;
  font-family: 'Share Tech Mono', monospace;
}

.resolution, .fps, .signal {
  background-color: rgba(0, 20, 0, 0.8);
  border: 1px solid rgba(0, 255, 0, 0.3);
  padding: 3px 8px;
}

.signal {
  position: relative;
  padding-left: 20px;
}

.signal::before {
  content: '';
  position: absolute;
  left: 5px;
  top: 50%;
  transform: translateY(-50%);
  width: 10px;
  height: 10px;
  background-color: #00ff00;
  border-radius: 50%;
  box-shadow: 0 0 5px #00ff00;
  animation: pulse 1.5s infinite;
}

/* No video state */
.no-video {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  text-align: center;
  color: #ff0000;
  font-size: 1.2rem;
  background-color: #080808;
  text-shadow: 0 0 8px rgba(255, 0, 0, 0.6);
  border: 2px solid #ff0000;
  animation: warning-pulse 2s infinite alternate;
}

@keyframes warning-pulse {
  from {
    box-shadow: inset 0 0 20px rgba(255, 0, 0, 0.3);
  }
  to {
    box-shadow: inset 0 0 50px rgba(255, 0, 0, 0.5);
  }
}

.no-video p {
  margin: 10px;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.7);
  border: 1px solid rgba(255, 0, 0, 0.5);
  width: 80%;
}

/* Add these styles */
.video-navigation {
  display: flex;
  align-items: center;
  gap: 15px;
}

.nav-btn {
  background-color: rgba(0, 20, 0, 0.7);
  border: 1px solid #00aa00;
  color: #00ff00;
  padding: 6px 12px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.nav-btn:hover {
  background-color: rgba(0, 40, 0, 0.9);
  box-shadow: 0 0 10px rgba(0, 255, 0, 0.4);
}

.video-progress {
  margin-top: 10px;
  width: 100%;
}

.progress-text {
  text-align: center;
  margin-bottom: 5px;
  font-size: 12px;
  color: #00aa00;
}

.progress-bar-container {
  width: 100%;
  height: 4px;
  background-color: rgba(0, 50, 0, 0.5);
}

.progress-bar {
  height: 100%;
  background-color: #00ff00;
  transition: width 0.3s ease;
}